.navbar{
    display: flex;
    box-shadow: rgba(0,0,0,.15) 0px -2px 10px 0px;
    justify-content: space-between;
    padding: 1em 5em;
   
}
.brand{
    font-size: 1.2em;
    margin-top: 1rem;
}
.brand span{
    font-weight: 900;
    text-transform: uppercase;
}
.links_list{
    display: flex;
    list-style: none;
}
.links_list li{
    margin-right: 1em;
}
.links_list li a{
    padding: 0.4em 0.6em;
   
}

.active{
    background-color: #000;
    color: white;
}